import React from 'react';
import MaterialTable from 'material-table';
import {connect} from "react-redux";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../Images/IconPack";
import TextField from "@material-ui/core/TextField/TextField";
import InputLabel from "@material-ui/core/InputLabel/InputLabel";
import {
    setNewValueForInviteStoreItem,
    createInviteItem,
    deleteInviteItem
} from "../../../redux/licenses/corporate/invite/invite.actions";
import Button from "@material-ui/core/Button";
import { FormattedMessage } from 'react-intl';

const CorporateInvite = (props) => {


    const addHandler = () => {
        const reducerStore =  props.inviteData.store;
        const reducerDefData =  props.inviteData.defValues;
        let ticketID = + new Date();


        let newObject = {
            login: reducerStore[reducerStore.length-1].login + 1,
            newLogin: reducerStore[reducerStore.length-1].login + 1,
            password: reducerDefData.password,
            nickname: reducerDefData.nickname,
            telegram: reducerDefData.telegram,
            comment: reducerDefData.comment,
            ticketID: ticketID,
        };

        props.createInviteItem(newObject);
    }


    const getRowItem = (itemData) => {
        return (<TableRow className="inputs-row">
            <TableCell className="padding-sides-10 color-white">
                <TextField
                    fullWidth
                    value={itemData.login}
                    onChange={(e)=> {props.setNewValueForInviteStoreItem('login', e.target.value, itemData.ticketID)}}
                    variant="filled"

                />
            </TableCell>
            <TableCell className="padding-sides-10 color-white">
                <TextField
                    fullWidth
                    value={'CU' + itemData.newLogin}
                    onChange={(e)=> {props.setNewValueForInviteStoreItem('newLogin', e.target.value, itemData.ticketID)}}
                    variant="filled"
                    disabled
                />
            </TableCell>
            <TableCell className="padding-sides-10 color-white">
                <TextField
                    fullWidth
                    value={itemData.password}
                    onChange={(e)=> {props.setNewValueForInviteStoreItem('password', e.target.value, itemData.ticketID)}}
                    variant="filled"
                />
            </TableCell>
            <TableCell className="padding-sides-10 color-white">
                <TextField
                    fullWidth
                    value={itemData.nickname}
                    onChange={(e)=> {props.setNewValueForInviteStoreItem('nickname', e.target.value, itemData.ticketID)}}
                    variant="filled"
                />
            </TableCell>
            <TableCell className="padding-sides-10 color-grey">
                <TextField
                    fullWidth
                    value={itemData.telegram}
                    onChange={(e)=> {props.setNewValueForInviteStoreItem('telegram', e.target.value, itemData.ticketID)}}
                    variant="filled"
                />
            </TableCell>
            <TableCell className="padding-sides-10 color-grey">
                <TextField
                    fullWidth
                    value={itemData.comment}
                    onChange={(e)=> {props.setNewValueForInviteStoreItem('comment', e.target.value, itemData.ticketID)}}
                    variant="filled"
                    className="w80p"
                />
                <div className="generateTable__btnsWrapper">
                    {props.inviteData.store.length > 1 ? (<Button className="btn--table" onClick={()=>{props.deleteInviteItem(itemData.ticketID)}}>{IconPack.buttons.minus}</Button>) : null }
                    <Button className="btn--table" onClick={()=>{addHandler()}}>{IconPack.buttons.plus}</Button>
                </div>
            </TableCell>
        </TableRow>)
    }




    return (
        <div className="сustomStyle">
            <div className="alertPanel">
                <p className="color-white">The users listed below will be invited to join your license. You can only invite users with whom you have an agreement on migration.</p>
                <span className="btn-text color-blue">MORE</span>
            </div>
            <Table className="configTable">
                <TableHead>
                    <TableRow>
                        <TableCell className="padding-sides-20 "><FormattedMessage id='table.currentLogin' /></TableCell>
                        <TableCell className="padding-sides-10 "><FormattedMessage id='table.newLogin' /></TableCell>
                        <TableCell className="padding-sides-10 "><FormattedMessage id='table.password' /></TableCell>
                        <TableCell className="padding-sides-10"><FormattedMessage id='table.nickname' /></TableCell>
                        <TableCell className="padding-sides-10"><FormattedMessage id='table.telegram' /></TableCell>
                        <TableCell className="padding-sides-10 w500"><FormattedMessage id='table.commentRoleOrMore' /></TableCell>
                    </TableRow>
                </TableHead>
                <TableBody className="no-hover">
                    {props.inviteData.store.map(itemData => {
                        return getRowItem(itemData);
                    })}
                </TableBody>
            </Table>
        </div>
    );
}

const mapStateToProps = (state) => ({
    inviteData: state.corporateLicenseInvite.invite,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setNewValueForInviteStoreItem: (item, itemValue, id) => dispatch(setNewValueForInviteStoreItem(item, itemValue, id)),
        createInviteItem: (object) => dispatch(createInviteItem(object)),
        deleteInviteItem: ( id ) => dispatch(deleteInviteItem( id )),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(CorporateInvite);